import {HTMLParser} from "../../common/js/common.js"
import common from '../../common/js/common.js'
var serverUrl = common.serverUrl
export default {
	data() {
		return {
			news:{},
			content:'',
			nodes:[],
			tabId:0,
			myPageHelper:{},
			startNum:2
			
		}
	},
	onLoad(data) {
		 var newsObj =JSON.parse(decodeURIComponent(data.newsStr)); 
		 this.news= newsObj
		 this.content=newsObj.htmlContent
		var title = newsObj.title
		uni.setNavigationBarTitle({
		    title: title
		});
		this.tabId=newsObj.tabId
		this.getRecommend()
	},
	onShareAppMessage: function(res) {
		var title = this.news.title
		return {
			title:title,
			path:'/pages/NewsDetail/NewsDetail'
		}
	},
	methods: {
		//保存文件
		saveDocument(url){
			var data = url
				uni.setClipboardData({
					data:data,
					success:function(){
						uni.getClipboardData({
							success:function(){
								uni.showToast({
									icon:"none",
									title:'下载链接已复制 请使用浏览器访问',
									duration:2000
								})
							}
						})
					}
				})
			  
		},
		//打开文件
		openDocument(url){
			uni.showLoading({
				title:'要等一等哟~',
			})
			var documentUrl = url
			uni.downloadFile({
			  url: documentUrl,
			  success: function (res) {
			    var filePath = res.tempFilePath;
			    uni.openDocument({
			      filePath: filePath,
			      success: function (res) {
					  uni.hideLoading();
			      }
			    });
			  }
			});
		},
		
		//请求推荐
		getRecommend(){
			uni.request({
				url:serverUrl+'/news/'+this.tabId+'?currentNum='+this.startNum+'&size=3',
				method:'GET',
				success: (res) => {
					if(res.data.code===0){
						this.myPageHelper = res.data.data
						
					}
					
				}
			})
		},
		//更换推荐
		changeRecommend(){
			// var nextPage = this.myPageHelper.pageNum+1
			this.startNum=this.startNum+1
			var nextPage = this.startNum
			var hasNextPage = this.myPageHelper.hasNextPage
			if(hasNextPage){
				uni.request({
					url:serverUrl+'/news/'+this.tabId+'?currentNum='+nextPage+'&size=3',
					method:'GET',
					success: (res) => {
						if(res.data.code===0){
							this.myPageHelper = res.data.data
						}
						
					}
				})
			}else{
				this.startNum=2
				this.getRecommend()
			}
		},
		goToNewsDetail(data){
				uni.navigateTo({
					url:'./NewsDetail?newsStr='+encodeURIComponent(JSON.stringify(data))
				})
			
			
		},
	}
}
